import { ConnectorInterface } from '@xip-online-data/types';

export type ComparisonResult = {
  onlyInA: Array<ConnectorInterface>;
  onlyInB: Array<ConnectorInterface>;
  inBoth: Array<ConnectorInterface>;
};

export function checkTwoArrays(
  datasetA: Array<ConnectorInterface>,
  datasetB: Array<ConnectorInterface>,
): ComparisonResult {
  const onlyInA: Array<ConnectorInterface> = [];
  const onlyInB: Array<ConnectorInterface> = [];
  const inBoth: Array<ConnectorInterface> = [];

  datasetA.forEach((itemA) => {
    const itemB = datasetB.find(
      (b) =>
        b.identifier === itemA.identifier &&
        b.connectorType === itemA.connectorType &&
        new Date(b.updatedAt).getTime() === new Date(itemA.updatedAt).getTime(),
    );
    if (itemB) {
      inBoth.push(itemA);
    } else {
      onlyInA.push(itemA);
    }
  });

  datasetB.forEach((itemB) => {
    const itemA = datasetA.find(
      (a) =>
        a.identifier === itemB.identifier &&
        a.connectorType === itemB.connectorType &&
        new Date(a.updatedAt).getTime() === new Date(itemB.updatedAt).getTime(),
    );
    if (!itemA) {
      onlyInB.push(itemB);
    }
  });

  return { onlyInA, onlyInB, inBoth };
}
